import { createTuple } from "../../../../../baseTypes";
import { verifyStandardMove } from "../../../MoveTree/MoveTree";
import { FENData } from "../../../FENData/FENData";
import { createDefaultFENEffectSettings } from "../../../FENData/FENDataInterface";
import { totalPlayers } from "../../../GameInformation/GameData";
import { createPieceFromData, deadColorIndex } from "../../../GameInformation/GameUnits/PieceString";
import { VariantRule } from "../../VariantRuleInterface";
import { RoyalsCannotCapture } from "../PieceControlDecorators/RoyalsCannotCapture";
import { pieceControlConfigSettings } from "@moveGeneration/PieceControl/PieceControlInterface";
const tag = "fatalCapture";
export class FatalCapture extends VariantRule {
    static {
        VariantRule.initVariantRule(FatalCapture);
    }
    dependencies = new Map([[RoyalsCannotCapture, []]]);
    getDecoratorType() { return FENData; }
    getParameterValue() { return true; }
    matchesPGNDeclaration(match) {
        return /^FatalCapture$/i.test(match);
    }
    serializeToParsingForm() {
        return "FatalCapture";
    }
    getInformation() {
        return { name: "Fatal Capture", description: "Pieces (but not pawns) die when they capture", tag };
    }
    affectOptions(move, settings = createDefaultFENEffectSettings()) {
        let endCaptureCoordinates = undefined;
        if (verifyStandardMove(move)) {
            const { startCoordinates: [startI, startJ], endCoordinates: [endI, endJ] } = move;
            const isCapture = this.decorator.board.board[endI][endJ].isPiece();
            const royal = this.decorator.fenOptions.royal[this.decorator.sideToMove];
            if (royal && startI === royal[0] && startJ === royal[1] && isCapture) {
                return {
                    checkmates: this.decorator.board.gameType.getBaseColors(this.decorator.sideToMove),
                    checks: createTuple(false, totalPlayers), stalemates: createTuple(false, totalPlayers)
                };
            }
            endCaptureCoordinates = isCapture ? move.endCoordinates : undefined;
        }
        let returnType;
        if (this.wrappingDecorator?.affectOptions) {
            returnType = this.wrappingDecorator.affectOptions(move, settings);
        }
        else {
            returnType = FENData.prototype.affectOptions.call(this.decorator, move, settings);
        }
        if (endCaptureCoordinates) {
            const pieceString = this.decorator.board.board[endCaptureCoordinates[0]][endCaptureCoordinates[1]];
            if (!pieceControlConfigSettings[pieceString.piece].moveGenerationSettings.isPawn) {
                this.decorator.board.board[endCaptureCoordinates[0]][endCaptureCoordinates[1]] = createPieceFromData(deadColorIndex, this.decorator.board.board[endCaptureCoordinates[0]][endCaptureCoordinates[1]].piece);
            }
        }
        return returnType;
    }
}
