import { boardDimension } from "@moveGeneration/GameInformation/GameData";
import { pieceControlConfigSettings } from "@moveGeneration/PieceControl/PieceControlInterface";
import { FENData } from "../../../FENData/FENData";
import { VariantRule } from "../../VariantRuleInterface";
import { RoyalsCannotCapture } from "../PieceControlDecorators/RoyalsCannotCapture";
const tag = "atomic";
export class Atomic extends VariantRule {
    static {
        VariantRule.initVariantRule(Atomic);
    }
    dependencies = new Map([[RoyalsCannotCapture, []]]);
    getDecoratorType() { return FENData; }
    getParameterValue() { return true; }
    matchesPGNDeclaration(match) {
        return /^Atomic$/i.test(match);
    }
    serializeToParsingForm() {
        return "Atomic";
    }
    getInformation() {
        return { name: "Atomic", description: "Captures cause all surrounding pieces except pawns to explode", tag };
    }
    isComplexEvaluation() {
        return true;
    }
    getCapturedPieces(moveData) {
        const baseCoordinates = this.wrappingDecorator?.getCapturedPieces
            ? this.wrappingDecorator.getCapturedPieces(moveData)
            : FENData.prototype.getCapturedPieces.call(this.decorator, moveData);
        const addCoordinate = (disI, disJ) => {
            const coordinateI = moveData.endCoordinates[0] + disI;
            const coordinateJ = moveData.endCoordinates[1] + disJ;
            if (coordinateI >= 0 && coordinateI <= boardDimension
                && coordinateJ >= 0 && coordinateJ <= boardDimension) {
                const pieceString = this.decorator.board.board[coordinateI][coordinateJ];
                if ((pieceString.isPiece() || pieceString.isDead())
                    && !pieceControlConfigSettings[pieceString.piece].moveGenerationSettings.isPawn) {
                    baseCoordinates.push([coordinateI, coordinateJ]);
                }
            }
        };
        if (baseCoordinates.length !== 0) {
            addCoordinate(-1, -1);
            addCoordinate(-1, 0);
            addCoordinate(0, -1);
            addCoordinate(-1, 1);
            addCoordinate(1, -1);
            addCoordinate(1, 0);
            addCoordinate(0, 1);
            addCoordinate(1, 1);
            baseCoordinates.push([...moveData.startCoordinates]);
        }
        return baseCoordinates;
    }
}
