import { createTupleFromCallback } from "../../../../../baseTypes";
import { Board } from "../../../Board/Board";
import { boardDimension } from "../../../GameInformation/GameData";
import { VariantRule } from "../../VariantRuleInterface";
const tag = "blindfold";
export class Blindfold extends VariantRule {
    static {
        VariantRule.initVariantRule(Blindfold);
    }
    getDecoratorType() { return Board; }
    getParameterValue() { return true; }
    matchesPGNDeclaration(match) {
        return /^Blindfold$/i.test(match);
    }
    serializeToParsingForm() {
        return "Blindfold";
    }
    getInformation() {
        return { name: "Blindfold", description: "Pieces are invisible", tag };
    }
    getSquareVisibility() {
        if (this.wrappingDecorator?.getSquareVisibility) {
            return this.wrappingDecorator.getSquareVisibility().map((r, i) => r.map((v, j) => {
                if (this.decorator.board[i][j].isWall()) {
                    return v;
                }
                else {
                    return [...v, 3 /* DisplaySettings.Blindfolded */];
                }
            }));
        }
        else {
            return createTupleFromCallback((_, i) => createTupleFromCallback((_, j) => {
                if (this.decorator.board[i][j].isWall()) {
                    return [];
                }
                else {
                    return [3 /* DisplaySettings.Blindfolded */];
                }
            }, boardDimension), boardDimension);
        }
    }
}
