import { Board } from "../../../Board/Board";
import { InternalMoveSignature } from "../../../MoveTree/MoveTreeInterface";
import { VariantRule } from "../../VariantRuleInterface";
const tag = "allowPassing";
export class AllowPassing extends VariantRule {
    static {
        VariantRule.initVariantRule(AllowPassing);
    }
    getDecoratorType() { return Board; }
    getParameterValue() { return true; }
    matchesPGNDeclaration(match) {
        return /^AllowPassing$/i.test(match);
    }
    getInformation() {
        return { name: "Allow Passing", description: "Players can pass instead of making a move", tag };
    }
    serializeToParsingForm() {
        return "AllowPassing";
    }
    getAllowedInternalMoves(sideToMove = this.decorator.data.sideToMove) {
        const passingMoves = this.decorator.isKingInCheck(sideToMove) ? [] : [{ type: InternalMoveSignature.Pass }];
        if (this.wrappingDecorator?.getAllowedInternalMoves) {
            return [...passingMoves, ...this.wrappingDecorator.getAllowedInternalMoves(sideToMove)];
        }
        else {
            return [...passingMoves, ...Board.prototype.getAllowedInternalMoves.call(this.decorator, sideToMove)];
        }
    }
}
