import { createTuple } from "../../../../../baseTypes";
import { totalPlayers } from "../../../GameInformation/GameData";
import { PieceControl } from "../../../PieceControl/PieceControl";
import { VariantRule } from "../../VariantRuleInterface";
const tag = "anyCapture";
export class AnyCapture extends VariantRule {
    static {
        VariantRule.initVariantRule(AnyCapture);
    }
    getDecoratorType() { return PieceControl; }
    getParameterValue() { return true; }
    matchesPGNDeclaration(match) {
        return /^AnyCapture$/i.test(match);
    }
    serializeToParsingForm() {
        return "AnyCapture";
    }
    getInformation() {
        return { name: "Any Capture", description: "You can capture your own pieces", tag };
    }
    configure(configuration) {
        const modifiedConfiguration = {
            ...configuration,
            immunePieces: createTuple(false, totalPlayers),
        };
        if (this.wrappingDecorator?.configure) {
            this.wrappingDecorator.configure(modifiedConfiguration);
        }
        else {
            PieceControl.prototype.configure.call(this.decorator, modifiedConfiguration);
        }
    }
}
