import { Board } from "../../../Board/Board";
import { stringifyCoordinate } from "../../../Board/BoardInterface";
import { InternalMoveSignature } from "../../../MoveTree/MoveTreeInterface";
import { VariantRule } from "../../VariantRuleInterface";
const tag = "barePieceRule";
export class BarePieceRule extends VariantRule {
    static {
        VariantRule.initVariantRule(BarePieceRule);
    }
    getDecoratorType() { return Board; }
    getParameterValue() { return true; }
    matchesPGNDeclaration(match) {
        return /^BarePieceLoses$/i.test(match);
    }
    getInformation() {
        return {
            name: "Bare piece rule",
            description: "Players with only one remaining piece are forfeit",
            tag
        };
    }
    serializeToParsingForm() {
        return "BarePieceLoses";
    }
    makeMove(move, ignoreNextMoves = false) {
        const results = this.wrappingDecorator?.makeMove
            ? this.wrappingDecorator.makeMove(move, ignoreNextMoves)
            : Board.prototype.makeMove.call(this.decorator, move, ignoreNextMoves);
        const playerPieces = this.decorator.getPlayerPieces()[this.decorator.data.sideToMove];
        if (playerPieces.length === 1) {
            const pieceMovements = this.decorator.preGeneratedAttacks.pieceMovements.get(stringifyCoordinate(playerPieces[0]));
            if (pieceMovements) {
                for (const { move: coordinate } of pieceMovements) {
                    if (this.decorator.board[coordinate[0]][coordinate[1]].isPiece()) {
                        return results;
                    }
                }
                return this.decorator.makeMove([{ type: InternalMoveSignature.Resign }]);
            }
        }
        return results;
    }
}
