import { formatOrdinalNumber } from "../../../../utils/StringFormatUtils";
import { boardDimension } from "../../../GameInformation/GameData";
import { PieceControl } from "../../../PieceControl/PieceControl";
import { VariantRule } from "../../VariantRuleInterface";
const tag = "promotionRank";
export const disabledRank = 99;
export class PromotionRank extends VariantRule {
    static {
        VariantRule.initVariantRule(PromotionRank);
    }
    promotionRank;
    constructor(promotionRank) {
        super();
        this.promotionRank = typeof promotionRank === 'number' ? promotionRank : 8;
    }
    getDecoratorType() { return PieceControl; }
    getParameterValue() { return this.promotionRank; }
    matchesPGNDeclaration(match) {
        const matchArray = match.match(/^Prom=(\d\d?)$/i);
        if (matchArray) {
            this.promotionRank = Number(matchArray[1]);
            return true;
        }
        else {
            return false;
        }
    }
    serializeToParsingForm() {
        return `Prom=${this.promotionRank}`;
    }
    getInformation() {
        return {
            name: "Promotion Rank",
            description: `on the ${this.promotionRank}${formatOrdinalNumber(this.promotionRank)} rank`,
            tag
        };
    }
    initDecoratorSettings() {
        if (this.decorator.hooks.usePawnLogic) {
            this.decorator.hooks.usePawnLogic.promotionRanks = [boardDimension - this.promotionRank,
                this.promotionRank - 1, this.promotionRank - 1, boardDimension - this.promotionRank];
        }
        if (this.wrappingDecorator?.initDecoratorSettings) {
            this.wrappingDecorator.initDecoratorSettings();
        }
    }
}
