import { compareCoordinates } from "../../../Board/BoardInterface";
import { FENData } from "../../../FENData/FENData";
import { createDefaultFENEffectSettings } from "../../../FENData/FENDataInterface";
import { colors, totalPlayers } from "../../../GameInformation/GameData";
import { verifyNumericColor } from "../../../GameInformation/GameUnits/GameUnits";
import { createPieceFromData, deadColorIndex } from "../../../GameInformation/GameUnits/PieceString";
import { InternalMoveSignature } from "../../../MoveTree/MoveTreeInterface";
import { pieceControlConfigSettings } from "../../../PieceControl/PieceControlInterface";
import { VariantRule } from "../../VariantRuleInterface";
const tag = "takeover";
export class Takeover extends VariantRule {
    static {
        VariantRule.initVariantRule(Takeover);
    }
    getDecoratorType() { return FENData; }
    getParameterValue() { return true; }
    matchesPGNDeclaration(match) {
        return /^Takeover$/i.test(match);
    }
    serializeToParsingForm() {
        return "Takeover";
    }
    getInformation() {
        return {
            name: "Takeover",
            description: "Checkmate players to take control of their pieces. Resigned armies move randomly",
            tag
        };
    }
    processInternalMove(internalMove) {
        if (internalMove.type === InternalMoveSignature.Resign
            || internalMove.type === InternalMoveSignature.ClaimWin
            || internalMove.type === InternalMoveSignature.Timeout) {
            const { sideToMove, fenOptions, board } = this.decorator;
            fenOptions.resigned[sideToMove] = true;
            fenOptions.dead[sideToMove] = true;
            for (const coordinate of board.getPlayerPieces()[sideToMove]) {
                const piece = board.board[coordinate[0]][coordinate[1]].piece;
                if (pieceControlConfigSettings[piece].moveGenerationSettings.isPawn) {
                    board.board[coordinate[0]][coordinate[1]] = createPieceFromData(deadColorIndex, piece);
                }
            }
            const deadPlayers = fenOptions.dead.map((d, i) => d || fenOptions.resigned[i]).filter(Boolean);
            if (deadPlayers.length === totalPlayers - 1) {
                const alivePlayer = deadPlayers.findIndex(d => !d);
                if (verifyNumericColor(alivePlayer)) {
                    this.decorator.assignPoints(alivePlayer, this.decorator.countTotalPointsOnBoard()
                        .reduce((p, n, i) => p + (i === alivePlayer ? n : 0), 0));
                }
            }
            return { stalemates: [false, false, false, false] };
        }
        else {
            return this.wrappingDecorator?.processInternalMove
                ? this.wrappingDecorator.processInternalMove(internalMove)
                : FENData.prototype.processInternalMove.call(this.decorator, internalMove);
        }
    }
    affectOptions(move, settings = createDefaultFENEffectSettings()) {
        const sideToMove = this.decorator.sideToMove, detectedPieces = this.decorator.board.getPlayerPieces();
        const results = this.wrappingDecorator?.affectOptions
            ? this.wrappingDecorator.affectOptions(move, settings)
            : FENData.prototype.affectOptions.call(this.decorator, move, settings);
        for (const color of colors) {
            if (!results.checkmates[color])
                continue;
            for (const piece of detectedPieces[sideToMove]) {
                const pieceString = this.decorator.board.board[piece[0]][piece[1]];
                if (!pieceString.isDead() || pieceControlConfigSettings[pieceString.piece].moveGenerationSettings.isPawn)
                    continue;
                this.decorator.board.board[piece[0]][piece[1]] = createPieceFromData(sideToMove, pieceString.piece);
            }
        }
        return results;
    }
    countTotalPointsOnBoard() {
        const resultingPoints = [0, 0, 0, 0];
        this.decorator.board.getPlayerPieces().forEach((army, color) => {
            if (this.decorator.fenOptions.zombieImmune[color])
                return;
            const royal = this.decorator.fenOptions.royal[color];
            for (const coordinate of army) {
                if (royal && compareCoordinates(coordinate, royal)) {
                    resultingPoints[color] += this.decorator.obtainPointsForMate();
                }
                else {
                    const piece = this.decorator.board.board[coordinate[0]][coordinate[1]].piece;
                    resultingPoints[color] += pieceControlConfigSettings[piece].points.singlesPoints;
                }
            }
        });
        return resultingPoints;
    }
}
