import { nonPlayablePieces } from "@moveGeneration/GameInformation/GameUnits/PieceString";
const nonPlayableValues = Object.values(nonPlayablePieces);
export const verifyPieceLetter = (piece) => piece in pieceControlConfigSettings || nonPlayableValues.includes(piece);
export const pieceControlConfigSettings = nonPlayableValues.reduce((p, n) => ({
    ...p, [n]: {
        points: {
            singlesPoints: 1,
            teamsPoints: 1,
            botFFAValue: 1,
            botTeamsValue: 1,
        },
        piece: n,
        moveGenerationSettings: {
            isComplex: false,
            isJumping: false,
            isSliding: false,
            isPawn: false,
        },
        naming: {
            name: "Wall",
            shortName: n
        }
    }
}), {});
export const createPieceDeclaration = (configuration) => {
    pieceControlConfigSettings[configuration.configuration.piece] = {
        ...configuration.configuration,
        construct: configuration.baseClassRef
    };
};
