import { verifyPieceLetter } from "@moveGeneration/PieceControl/PieceControlInterface";
import { colorEnum, stringColorEnum } from "./GameUnits";
export const deadColorIndex = 4;
export const nonPlayablePieces = {
    wall: "X", transparentWall: "x", duck: "Θ"
};
export class PieceString {
    _color;
    _piece;
    static comparePieceStrings(pieceString1, pieceString2) {
        return pieceString1._piece === pieceString2._piece && pieceString1._color === pieceString2._color;
    }
    value;
    constructor(_color, _piece) {
        this._color = _color;
        this._piece = _piece;
        this.value = _piece.length ? /[xXΘ]/.test(_piece) ? _piece : stringColorEnum[_color] + _piece : '';
    }
    isDead() { return this._color === deadColorIndex && !this.isWall(); }
    isEmpty() { return this._piece.length === 0; }
    isWall() { return this.value.length === 1; }
    isPiece() { return this.value.length === 2 && this._color !== deadColorIndex; }
    get piece() {
        if (this.isEmpty())
            throw new TypeError("Accessing a piece string piece value while the piece string is empty.");
        return this._piece;
    }
    get color() {
        if (this.isEmpty())
            throw new TypeError("Accessing a piece string color value while the piece string is empty.");
        return this._color;
    }
    getStringifiedColor() {
        return this.value.charAt(0);
    }
}
export const createPieceFromData = (color, piece) => {
    if (verifyPieceLetter(piece)) {
        return new PieceString(color, piece);
    }
    throw new Error("Wrong color signature for the piece provided");
};
export const createPieceFromString = (piece) => {
    const verifyColorEnum = (p) => p in colorEnum;
    if (piece.length === 1 && verifyPieceLetter(piece)) {
        return new PieceString(deadColorIndex, piece);
    }
    else {
        const color = piece.charAt(0);
        const pieceLetter = piece.charAt(1);
        if (!verifyPieceLetter(pieceLetter))
            throw new Error("Wrong signature for the piece provided");
        if (color === 'd') {
            return new PieceString(deadColorIndex, pieceLetter);
        }
        else {
            if (verifyColorEnum(color)) {
                return new PieceString(colorEnum[color], pieceLetter);
            }
            else
                throw new Error("Wrong color signature for the piece provided");
        }
    }
};
export const emptyPieceString = new PieceString(0, "");
export const wallPieceString = new PieceString(deadColorIndex, "X");
export const pawnPieceString = new PieceString(0, "P");
export const duckPieceString = new PieceString(deadColorIndex, "Θ");
