export const verifyNumericColor = (num) => num >= 0 && num < 4;
export const colorEnum = {
    r: 0, b: 1, y: 2, g: 3
};
export const verifyColorEnumValue = (value) => value in colorEnum;
export const playerEnum = {
    Red: 0,
    Blue: 1,
    Yellow: 2,
    Green: 3,
    White: 0,
    Black: 2
};
export const verifyPlayerEnumValue = (player) => player in playerEnum;
export const stringColorEnum = {
    [0]: 'r', [1]: 'b', [2]: 'y', [3]: 'g', [4]: 'd',
};
